}
.box-card{
    width: 22%;
    height: 490px;
    display: flex;
    background: #fff;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    padding:10px;
    border-radius: 6px;
    box-shadow: 0 0 20px rgb(0,0,0, .6);
    transition: ease-in-out .5s;
}
.box-card:hover{
    transform: scale(1.04);

}
.box-card img{
    width: 100%;
    height: 150px;
    margin-top: -3px;
    border-radius: 5px;
}
.box-card h2{
    text-shadow:1px 2px 2px rgb(0,0,0, .3);
}
.faixa{
    width: 70%;
    height: 4px;
    background-image:linear-gradient(to right, #9ACD32,#228B22) ;
    border-radius: 5px;
